import React, { Component } from 'react';
import './App.css';
import { Layout, Menu } from 'antd';
import 'antd/dist/antd.css';
import './Navi.css'
import Links from './Links'
import PicShow from './PicShow'
const mountain = require.context("./image/mountain",true, /^\.\/.*\.jpg$/);
const flowers = require.context("./image/flowers",true, /^\.\/.*\.jpg$/);
const pure = require.context("./image/pure",true, /^\.\/.*\.jpg$/);
const sexy = require.context("./image/sexy",true, /^\.\/.*\.jpg$/);
const uniform = require.context("./image/uniform",true, /^\.\/.*\.jpg$/);
const temperament = require.context("./image/temperament",true, /^\.\/.*\.jpg$/);
const lovely = require.context("./image/lovely",true, /^\.\/.*\.jpg$/);
const science = require.context("./image/science",true, /^\.\/.*\.jpg$/);
const abst = require.context("./image/abst",true, /^\.\/.*\.jpg$/);


const picGroup = {
	mountain:mountain.keys().map(mountain), 
	flowers:flowers.keys().map(flowers), 
	pure:pure.keys().map(pure), 
	sexy:sexy.keys().map(sexy), 
	uniform:uniform.keys().map(uniform), 
	temperament:temperament.keys().map(temperament), 
	lovely:lovely.keys().map(lovely), 
	science:science.keys().map(science), 
	abst:abst.keys().map(abst)
}
		
const projectImgs = mountain.keys().map(mountain);


const { Header, Footer, Content } = Layout;


const menus = [
{code:"mountain", name:"山川"},
{code:"flowers", name:"花草"},
{code:"pure", name:"清纯"},
{code:"sexy", name:"性感"},
{code:"uniform", name:"制服"},
{code:"temperament", name:"气质"},
{code:"lovely", name:"可爱"},
{code:"science", name:"科幻"},
{code:"abst", name:"抽象"}
];         
                
const links = [{key:0, url:"http://www.jiandan.com", title:"简单搜索"},
{key:1, url:"http://www.baidu.com", title:"百度搜索"},
{key:2, url:"http://www.jd.com", title:"京东商城"},
{key:3, url:"http://www.alibaba.com", title:"阿里巴巴"},
{key:4, url:"http://www.dongfang.com", title:"东方财富"},
{key:5, url:"http://www.toutiao.com", title:"今日头条"}];




class App extends Component {
	 constructor(props) {
        super(props);
        this.init = this.init.bind(this);
        let state = this.init(props);
		state.menu = 'pure';
		state.start = 1;
		state.img_list = picGroup[state.menu];
        this.state = state;
    }
	buildPath(code){
		console.log("code:", code);
		console.log("image:", "./image/" + code);
		return ("./image/" + code);
	}
	changeMenu( code ) {
		console.log("code:", code);
		this.setState({menu:code})
		if(picGroup[code]){
			this.setState({img_list:picGroup[code], start:1})
		}
    }
	init( props ){
		let state = {
			img_list : projectImgs
		}
		return state;
	}



	
  render() {
    return (
     <div>
    <Layout>
      <Header><div className="logo"></div>
      <Menu
        theme="dark"
        mode="horizontal"
        defaultSelectedKeys={this.state.menu}
        style={{ lineHeight: '64px' }}
      >
	  {
		  menus.map((item, index) => { 
			  return (
			  <Menu.Item key={item.code} onClick={this.changeMenu.bind(this , item.code )}>{item.name}</Menu.Item>
			  )
		  })
	  }
      </Menu>
</Header>
      <Content style={{ margin: '0 16px' }}>

	  <PicShow
        width="100%"
        height="500"
		animate="fade"
		delay="1000"
		wait="3000"
        mode="style"
		start={this.state.start}
        img={this.state.img_list} />
	  </Content>
      <Footer style={{ textAlign: 'center' }}>
	  <Links links={links}/>
	  Ant Design ©2016 Created by Ant UED</Footer>
    </Layout>
      </div>
    );
  }
}

export default App;
