import React, { Component } from 'react';

class Links extends Component {
	
	render() {
		
		if(this.props.links){
			const links = this.props.links.map((item) => 
			<a className='linkClass' key={item.key} href={item.url} rel="noopener noreferrer">{item.title}</a>);
			return (
				<div>
				{links}
				</div>
			);
		}
	}
}

export default Links;
